@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

.main-main{

  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-top: 3rem; */
  /* border: 1px solid black; */


 

}
.main{
  /* width: 95vw; */
  padding: 1rem 5rem;
  /* height:200vh; */

}
.main-container{
  /* height: 20vh; */
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: 1px solid black;
}
.image-container{
  width: 10vw;
}
.text-container{
  width: 35rem;
}
.doc-no{
display: flex;
margin-top: 1rem;
}


.doc-no li {
  list-style-type: none;
  font-size: 13px;
  height: 1.7rem;
  margin-top: -0.2rem;
  width:7rem
}
.doc-no-sec li {
  margin-left: -2.7rem;
}
.container{
  min-width: 100vw;
}

.home{
width: 100vw;
height: 30vh;
margin-top: 5rem;
display: flex;
flex-direction: column;
justify-content: space-between;
align-items: center;
}
.home-dropdown-cal{
  margin-right: 1rem;
}
.carousel{
  width: 100vw;
  height: 65vh;
  object-fit: cover;
  object-position: 1% 5%;
}
.datepic{
  display: flex;
  align-items: center;
}
.usermain-nav{
  height: 100vh;
}
.datepicker-1{
  background-color: #FFB72B;;
  height: 2.7rem;
  border: none;
  cursor: pointer;
  
}
.datepicker-1::placeholder, .datepicker-2::placeholder{
  color: rgb(255, 255, 255);
  letter-spacing: 1px;
  text-align: center;
  font-weight: 700;
  font-family: Arial, Helvetica, sans-serif;

}
.datepicker-2{
  background-color: #2FA4FF;
  height: 2.7rem;
  border: none;
  cursor: pointer;
  margin-left: -10rem;
  
}
.span{
  font-weight: 900;
  color: red;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.planla{
  color:red;
  
}
.planlama{
  color:green
}
.buttonusermain{
  height: 1rem;
  width:1rem;
  color: #ff792b;
  size: 5rem;
}
