
  :root {
    /*   color variables */
    --clr-primary: #ee6c6a;
    --clr-primary-hover: #29e6a7;
    --clr-primary-dark: #039d69;
    --clr-gray100: #f0f7f8;
    --clr-gray200: #cfd8dc;
  
    /*   border radius */
    --radius: 0.2rem;
  }
  
  *,
  *:before,
  *:after {
    box-sizing: border-box;
  }
  .body {
    font-family: Montserrat, sans-serif;
    background: var(--clr-primary);
  }
  
  .nav-list {
    background-color: rgb(244, 237, 237);
    box-shadow: 0px 0px 10px var(--clr-gray200);
    padding: 1rem 0;
    border-radius: var(--radius);
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  
  .nav-item {
    list-style: none;
    margin-right: 2rem;
    font-weight: 700;  
    /* height: 0rem; */
  }
  .nav-item:hover {
    border:1px solid rgb(10, 1, 1);
    background-color: #ffffff;
    border-radius: 2rem;
       

    
  

 
  }
  
  .nav-item a {
    text-decoration: none;
    color: RGB(161, 16, 16);
        padding: 1rem;
    
    
  }
  
  .nav-item a:hover {
    color: var(--clr-primary-dark);
    /* cursor: pointer; */
     height: 7rem;
                transition: all 0.9s ease-in-out;
  }
  
  .nav-item:first-child {
    margin-left: auto;
    margin-left: 2rem;
  }
  .nav-item:nth-child(2){
    margin-right: auto;
    /* margin-left: 2rem; */
  }
  .nav-item:nth-child(6)  {
   color:rgb(11, 26, 243)
  }
  .nav-item:nth-child(6):hover  {
border: none;
background-color: none;
  }
  
  .btn-primary {
    font-family: Montserrat, sans-serif;
    font-size: 1rem;
    background-color: var(--clr-primary);
    border: none;
    outline: none;
    border-radius: var(--radius);
    padding: 0.5rem 1rem;
    cursor: pointer;
    transition: all 200ms ease-in;
  }
  
  .btn-primary:hover {
    background: var(--clr-primary-hover);
  }
  